.navbar {
    list-style-type: none;
    display: flex;
    justify-content: space-around;
    padding: 10px 20px; 
    background-color: #f0f0f0; 
    border-radius: 20px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    margin: 20px;
    max-width: 500px;
    margin-left: auto;
    margin-right: auto;
  }
  
  .navbar li {
    margin: 0 10px;
    display: inline-block;
  }
  
  .navbar a {
    text-decoration: none;
    color: #333;
    font-weight: bold;
    padding: 5px 10px;
    border-radius: 10px;
    transition: background-color 0.3s;
  }
  
  .navbar a:hover {
    background-color: rgba(0, 0, 0, 0.05);
  }
  
    
    .profile-container {
      display: flex;
      justify-content: space-between;
      padding: 50px;
    }
    
    .profile-text {
      flex: 1;
      padding-right: 50px;
    }
    
    .profile-image {
      flex: 1;
      /* Adjust this to fit your image */
      background-image: url('data:image/png;base64,');
      background-size: cover;
      background-position: center;
    }
  
    